import React, { useState } from "react";
import { Navigate } from "react-router-dom";

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import ThreeSixtyIcon from '@mui/icons-material/ThreeSixty';
import AlarmIcon from '@mui/icons-material/Alarm';
import { Button } from "@mui/material";
export const PlayerTab = ({ timeWiner, allGames, user, setTimeWiner, setUser }) => {

    const [restart, setRestart] = useState(false);
    const [sortAscending, setSortAscending] = useState(true);
    // const sortAllGames = (sortByField, sortAscending) => {

    //     let sortedArray = [];

    //     if (Array.isArray(allGames.times)) {
    //         sortedArray = allGames.times.slice().sort((a, b) => {
    //             // Преобразование времени в миллисекунды для сравнения
    //             const timeA = timeStringToMilliseconds(a);
    //             const timeB = timeStringToMilliseconds(b);

    //             return timeA - timeB;
    //         });
    //     }


    //     // Если порядок сортировки установлен на убывающий, перевернуть массив
    //     if (!sortAscending) {
    //         sortedArray.reverse();
    //     }

    //     return sortedArray;
    // };

    // const timeStringToMilliseconds = (time) => {
    //     const [hours, minutes, seconds] = time.split(":").map(Number);
    //     return hours * 3600000 + minutes * 60000 + seconds * 1000;
    // };

    return (
        <div >
            {user.name === "" && <Navigate to="/login" />}
            {restart && <Navigate to="/game" />}
            {timeWiner === null ? (
                <div>load</div>
            ) : (
                <div className="tab-box">
                    <div>
                        <h3 style={{ paddingLeft: "15px" }}>Last game - {user.name}</h3>
                        <div style={{ paddingLeft: "20px", marginBottom: "15px" }}>{timeWiner}</div>
                        <Button style={{ marginRight: "10px" }} variant="contained" color="success" size="small"
                            onClick={() => {
                                setUser({ name: "" })
                                setTimeWiner(null)
                                setSortAscending(false)
                            }}>Add user
                        </Button>
                        <Button
                            onClick={() => {
                                setRestart(true)
                                setTimeWiner(null)
                                setSortAscending(false)
                            }}
                            variant="outlined" size="small">
                            Restart
                            <ThreeSixtyIcon />
                        </Button>
                    </div>
                    <TableContainer component={Paper} style={{ minWidth: "300px" }}>
                        <Table aria-label="simple table" >
                            <TableHead >
                                <TableRow>
                                    <TableCell>Name</TableCell>
                                    <TableCell className={sortAscending ? "time-box active" : "time-box"} align="right" onClick={() => setSortAscending(!sortAscending)}>
                                        Time <AlarmIcon className="time-box__icon" />
                                    </TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {Array.isArray(allGames.names) && allGames.names.map((name, index) => (
                                    <TableRow
                                        key={index}
                                        sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                    >
                                        <TableCell component="th" scope="row">
                                            {name}
                                        </TableCell>
                                        <TableCell align="right">{allGames.times[index]}</TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div>
            )
            }
        </div >
    );
};
